//  schema uniCloudschema
// uniCloud: https://unicloud.dcloud.net.cn/



export default {
  "username": {
    "rules": [
      {
        "required": true,
        "errorMessage": "{label}"
      },
      {
        "format": "string"
      },
      {
        "minLength": 2
      }
    ],
    "label": ""
  },
  "password": {
    "rules": [
      {
        "required": true,
        "errorMessage": "{label}"
      },
      {
        "format": "string"
      },
      {
        "minLength": 6
      }
    ],
    "label": ""
  },
  "mobile": {
    "rules": [
      {
        "format": "string"
      },
      {
        "pattern": "^\\+?[0-9-]{3,20}$"
      }
    ],
    "label": ""
  },
  "email": {
    "rules": [
      {
        "format": "string"
      },
	  {
	    "format": "email"
	  }
    ],
    "label": ""
  },
  "role": {
    "rules": [
      {
        "format": "array"
      }
    ]
  }
}
